$trs1: .25s ease-out;
$trs2: .5s ease-out;
$trs3: 1s ease-out;

.new-tabs__panel-type-header {
  justify-content: space-between;
  padding-right: 15px;
  padding-left: 15px;
}
.new-tabs__linkShell {
  flex: 1 1 25%;
  &:first-child {
    display: none;
  }
  .new-tabs__link {
    margin: 0 auto;
  }
}
header.new-header {
  min-width: 320px;
}
@media (min-width: 769px) {
  .new-tabs__link-type-header {
    justify-content: center;
  }
}

@media (min-width: 1025px) {
  .new-header__top {
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
    padding: 15px;
    align-items: center;
  }
  .new-tabs__linkShell {
    flex: 0 0 auto;
    &:first-child {
      display: block;
      margin: 0;
    }
  }
  .new-console__bag, .new-console__button, .new-console__compare {
    padding: 10px 0;
    &:not(:last-child) {
      margin: 0 20px 0 0;
    }
  }
}

@media (min-width: 1200px) {
  .new-tabs__link-type-desktop {
    padding: 15px;
  }
  .new-tabs__panel-type-header {
    padding-right: 0;
    padding-left: 0;
  }
}

.new-slider__card,
.new-card-type-catalog {
  height: auto;
  padding: 20px 15px 16px 15px;
}
.new-card {
  &__top {
    margin-bottom: 5px;
  }
  &__center {
    margin-bottom: 5px;
  }
  &__labels {
    top: 20px;
  }
}

.search-header {
  padding: 20px 0;
}
.filter {
  left: 0;
}

@media (max-width: 1024px) {
  .search-header {
    padding: 20px 15px;
  }
  .page {
    width: 100%;
  }
  .content {
    padding: 15px;
  }
  .content .new-content__catalog {
    margin: -15px;
  }
  .new-content__left {
    padding-left: 15px;
    padding-right: 15px;
  }
  .new-console__count {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0;
    width: 100%;
    text-align: center;
    line-height: 15px;
    background-color: transparent;
    color: #000;
    font-size: 11px;
  }
  .new-console__bag {
    position: relative;
  }
  .new-console__icon-type-bag {
    margin: 0;
  }
}
@media (max-width: 768px) {
  .new-header__search {
    display: none;
  }
  .content .new-content__catalog {
    margin: -10px;
  }
}

@media (max-width: 575px) {
  .new-header__slider {
    .slick-slide {
      position: relative;
      &::before {
        content: '';
        display: block;
        padding-top: 50%;
      }
    }
    .new-slider__img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
}

//search
.new-console__icon-type-search {
  stroke: #333;
  stroke-width: 1.5px;
}
.mobile-search {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  padding: 0 15px;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: opacity $trs1, visibility $trs1;
  &.active {
    opacity: 1;
    visibility: visible;
  }
  .new-search__input {
    padding: 8px 10px;
    box-shadow: 0 0 5px 0 rgba(0,0,0,.075);
  }
  &__form {
    position: relative;
    flex: 1 1 100%;
  }
  &__close {
    display: block;
    width: 30px;
    height: 30px;
    position: relative;
    flex: 0 0 auto;
    margin: 0 -6px 0 9px;
    &::before,
    &:after {
      content: '';
      position: absolute;
      width: 22px;
      height: 1px;
      background: #333;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%) rotate(45deg);
    }
    &:after {
      transform: translate(-50%,-50%) rotate(-45deg);
    }
  }
}

@media (min-width: 769px) {
  .mobile-search,
  .mobile-search-open {
    display: none;
  }
}